import React from "react"
import Button from "./Button"
import CryptoJS from "crypto-js"
import { connect } from "react-redux"

import { updateSession } from "../../state/actionTypes"

// START of ResetBUTTON CLASS
const ResetButton = ({
  apply,
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
  accounts,
  expenses,
  groupings,
}) => {
  const encrypt = () => {
    const state = JSON.stringify({
      calculator,
      portfolio,
      setup,
      transactions,
      analysis,
      accounts,
      expenses,
      groupings,
    })

    // Encrypt
    const ciphertext = CryptoJS.AES.encrypt(state, "PengEncryption").toString()
    apply(ciphertext)
  }

  const decrypt = () => {
    // Decrypt
    let ciphertext
    const bytes = CryptoJS.AES.decrypt(ciphertext, "PengEncryption")
    const originalText = bytes.toString(CryptoJS.enc.Utf8)

    console.log(originalText) // 'my message'
  }

  return <Button label="" colour="orange" handleClick={encrypt} />
}

const mapStateToProps = ({
  calculator,
  portfolio,
  setup,
  transactions,
  analysis,
  accounts,
  expenses,
  groupings,
}) => {
  return {
    calculator,
    portfolio,
    setup,
    transactions,
    analysis,
    accounts,
    expenses,
    groupings,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    apply: ciphertext => {
      dispatch(updateSession(ciphertext))
    },
  }
}

const ConnectedResetButton = connect(
  mapStateToProps,
  mapDispatchToProps
)(ResetButton)

export default ConnectedResetButton
