import React, { useState } from "react"
import Well from "./Well"
import Button from "./Button"
import Switch from "./Switch"
import Input from "./Input"
import ConnectedLoadButton from "./ConnectedLoadButton"
import ConnectedResetButton from "./ConnectedResetButton"
import ConnectedClearButton from "./ConnectedClearButton"

import styles from "../../styles/budget/tools.module.scss"

// START OF SETTINGS CLASS
const Settings = () => {
  const [smartMode, setSmartMode] = useState(true)

  const handleSwitch = () => setSmartMode(!smartMode)

  const handleDumbClick = () => {
    if (smartMode === true) handleSwitch()
  }

  const handleSmartClick = () => {
    if (smartMode === false) handleSwitch()
  }

  return (
    <Well>
      <div className={styles.IDSwitchSeperator}>
        <div className={styles.IDInputSeperator}>
          <div className={styles.flexCenter}>
            <label htmlFor="Session ID">Session ID</label>
            <Input
              type={"text"}
              name={"Session ID"}
              placeholder={["Provide Session ID"]}
            />
          </div>
          <div className={styles.flexCenter}>
            <ConnectedLoadButton />
            <ConnectedResetButton />
            <ConnectedClearButton />
          </div>
        </div>
        <div className={styles.flexCenter}>
          <Button label="" colour="red" handleClick={handleDumbClick} />
          <Switch status={smartMode} handleSwitch={handleSwitch} />
          <Button label="" colour="green" handleClick={handleSmartClick} />
        </div>
      </div>
    </Well>
  )
}

export default Settings
