import React from "react"
import Button from "./Button"
import { connect } from "react-redux"

import { updateSession, clear } from "../../state/actionTypes"

import { clearButtonTypes } from "../../types/budget/types"

// START of CLEARBUTTON CLASS
const ClearButton = ({ clearAll }) => (
  <Button label="Clear" colour="red" handleClick={clearAll} />
)

ClearButton.propTypes = clearButtonTypes

const mapStateToProps = ({ value }) => {
  return { value }
}

const mapDispatchToProps = dispatch => {
  return {
    clearAll: () => {
      dispatch(updateSession(""))
      dispatch(clear())
    },
  }
}

const ConnectedClearButton = connect(
  mapStateToProps,
  mapDispatchToProps
)(ClearButton)

export default ConnectedClearButton
