import React from "react"
import Button from "./Button"
import CryptoJS from "crypto-js"
import { connect } from "react-redux"

import { load } from "../../state/actionTypes"

// START of LoadBUTTON CLASS
const LoadButton = ({ session, apply }) => {
  const decrypt = () => {
    // Decrypt
    let ciphertext = session
    const bytes = CryptoJS.AES.decrypt(ciphertext, "PengEncryption")
    const original = JSON.parse(bytes.toString(CryptoJS.enc.Utf8))
    apply(original)
  }

  return <Button label="Load" colour="green" handleClick={decrypt} />
}

const mapStateToProps = ({ session }) => {
  return {
    session,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    apply: obj => {
      dispatch(load(obj))
    },
  }
}

const ConnectedLoadButton = connect(
  mapStateToProps,
  mapDispatchToProps
)(LoadButton)

export default ConnectedLoadButton
